* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {}

.app__section {
  display: flex;
  padding: 30px;
  flex-direction: row;
}

@media (max-width: 800px) {
  .app__section {
    flex-direction: column;
  }
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 100;
  width: 100%;
  top: 0;
}

.header__icon {
  object-fit: contain;
  height: 100px;
  margin-left: 80px;
}

.header__center {
  flex: 2 1;
  display: flex;
  max-width: 200px;
  align-items: center;
  padding: 10px;
  height: 30px;
}

.header__center>input {
  border: none;
  width: 340px;
  height: 35px;
  border: 2px solid lightgray;
  border-radius: 999px;
}

.header__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15vw;
  margin-right: 80px;
}

.header__center>input {
  padding: 10px;
  outline-width: 0;
}

@media (max-width : 800px) {
  .header {
    display: block;
  }

  .header__icon {
    display: none;
  }

  .header__center>input {
    height: 40px;
    border: 2px solid lightgray;
    border-radius: 999px;
  }

  .header__center {
    display: block;
    max-width: 80%;
    margin: auto;
    /* align-items: end; */
    /* padding: 7px;
    height: 40px;
    border: 1px solid lightgray;
    border-radius: 999px;
    margin: auto; */
    /* margin-top: 15px;
    margin-bottom: 10px; */

  }

  i {
    display: flex;
    float: right;
    margin: auto;
    margin-top: 10px;
    margin-left: 0;
  }

  .header__right {
    display: none;
  }
}
.footer {
  border-top: 1px solid lightgray;
  padding: 20px;
  background-color: #f7f7f7;
  bottom: 0;
  text-align: center;
  bottom: 0;
}
.footer > p {
  padding: 5px;
  font-size: 14px;
}

.banner {
  background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen-28328347.jpg")
    center center;
  height: 50vh;
  position: relative;
}
h5{
  text-align: justify;
}

.banner__search {
  top: 0;
  display: flex;
  flex-direction: column;
}

.banner__info {
  background-color: black;
  /* padding-top: 25vh;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px; */
  width: 350px;
  height: 45vh;
  color: white;
}

.banner__searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.banner__info > button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit;
  margin-top: 20px;
  font-weight: 600;
  margin-left: 25%;
}
/* .button_div{
  width: 90%;
  margin: auto;
} */
.banner__info > button:hover {
  background-color: white !important;
  color: #ff7779;
}

.banner__info >  {
  margin-top: 10px;
}

@media (max-width : 800px) {
  .banner{
    display: none;
  }
}


.search {
  position: absolute;
  top: 35px;
  left: 25%;
  width: 100vw;
}

.search > h2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 559px;
  padding: 10px;
  background-color: white;
  position: absolute;
  left: 0;
  top: 380px;
}

.search > input {
  width: 539px;
  padding: 20px;
  position: absolute;
  left: 0;
  height: 30px;
  top: 420px;
  border: none;
}

.search > input:focus {
  outline-width: 0;
}

.search > button {
  position: absolute;
  left: 0;
  top: 480px;
  text-transform: inherit !important;
  background-color: #ff7779;
  color: white;
  width: 579px;
}

.search > button:hover {
  background-color: white;
  color: #ff7779;
}

.card {
  margin: 10px;
  border: 0px solid black;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
  cursor: pointer;
}


.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card > img {
  object-fit: fill;
  min-width: 300px;
  min-height: 200px;
  width: 100%;
}

.card__info {
  margin-top: -9px;
  border-radius: 10px;
  padding: 20px;
  padding-top: 20px;
  border: 1;
}

.card__info > h2 {
  font-size: 18px;
  font-weight: 600;
}

.card__info > h4 {
  font-size: 14px;
  font-weight: 300;
  margin-top: 8px;
  margin-bottom: 8px;
}

.rowcont {
  position: relative;
}

.imgLike {
  width: 95%;
  margin: auto;
}

.imgLike img {
  width: 93%;
  height: 90%;
  border-radius: 10px;
}
.searchResult:hover {
  opacity: 0.8;
}



.lbtn {
  position: absolute;
  top: 42px;
  right: -2px;
  color: red;
  font-size: 30px;
  z-index: 1;
}


button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.on {
  color: red;
  font-size: 30px;
}

.off {
  color: #ccc;
}
.searchPage__info {
  padding: 20px;
}

.searchPage__info > p {
  margin-bottom: 10px;
  font-size: 14px;
}

.searchPage__info > h1 {
  margin-bottom: 30px;
}

.searchPage__info > button {
